:root {
  --primary: #3498db;
  --secondary: #2c3e50;
  --accent: #e74c3c;
}

body {
  color: #fff;
  background: linear-gradient(135deg, #001f3f 0%, #002851 100%);
  min-height: 100vh;
  margin: 0;
  font-family: Inter, system-ui, sans-serif;
  line-height: 1.6;
}

.loading-indicator {
  opacity: 0;
  backdrop-filter: blur(10px);
  background: #000000e6;
  border-radius: 1rem;
  padding: 1.5rem 3rem;
  transition: opacity .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 30px #0003;
}

.loading-indicator.visible {
  opacity: 1;
}

.loading-indicator .loader {
  border: 4px solid #fff3;
  border-top: 4px solid var(--primary);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: 0 auto 1rem;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hero {
  text-align: center;
  margin: 4rem 0;
}

.hero h1 {
  margin-bottom: 1.5rem;
  font-size: 3.5rem;
  line-height: 1.2;
}

.hero h1 span {
  color: var(--primary);
}

.features-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 4rem auto;
  padding: 0 2rem;
  display: grid;
}

.feature-card {
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 1.5rem;
  padding: 2.5rem;
  transition: transform .3s, box-shadow .3s;
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px #0000004d;
}

.feature-icon {
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
}

.cta-button {
  background: var(--primary);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: .75rem;
  padding: 1.25rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .3s;
}

.cta-button:hover {
  background: #2980b9;
  transform: scale(1.05);
}

.deeplink-error {
  text-align: center;
  background: #ffffff1a;
  border-radius: 1.5rem;
  max-width: 600px;
  margin: 3rem auto;
  padding: 3rem;
}

.deeplink-error h2 {
  color: #e74c3c;
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

.action-buttons {
  margin-top: 2rem;
}

@media (width <= 768px) {
  .hero h1 {
    font-size: 2.5rem;
  }
}
